.container { width: 100%; padding-right: 15px; padding-left: 15px; margin-right: auto; margin-left: auto; }

@media (min-width: 576px) { .container { max-width: 540px; } }

@media (min-width: 768px) { .container { max-width: 720px; } }

@media (min-width: 992px) { .container { max-width: 960px; } }

@media (min-width: 1200px) { .container { max-width: 1300px; } }

[v-cloak] { display: none; }

h1, h2, h3, h4, h5, h6 { margin-bottom: .5rem; font-family: inherit; font-weight: 500; line-height: 1.2; color: inherit; }

h1 { font-size: 2.5rem; }

h2 { font-size: 2rem; }

h3 { font-size: 1.75rem; }

h4 { font-size: 1.5rem; }

h5 { font-size: 1.25rem; }

h6 { font-size: 1rem; }

.btn { display: inline-block; font-weight: 400; text-align: center; white-space: nowrap; vertical-align: middle; user-select: none; border: 1px solid transparent; padding: .375rem .75rem; font-size: 1rem; line-height: 1.5; border-radius: .25rem; transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out; color: #fff; background-color: #007bff; border-color: #007bff; text-decoration: none; }

.btn:active { color: #fff; background-color: #0062cc; border-color: #005cbf; box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5); }

.btn:focus { box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5); }

.btn:hover { color: #fff; background-color: #0069d9; border-color: #0062cc; transition: 0.5s; }

.nav-item { padding: 1em; display: inline; }

.nav-item-dropdown { position: relative; }

.nav-item-dropdown:hover > .dropdown-menu { animation: menu 0.7s ease-in-out; display: block; opacity: 1; }

.dropdown-menu { background-color: #ED3E44; display: block; text-align: right; position: absolute; top: 2.5rem; right: -10px; display: none; opacity: 0; width: 260px; }

.dropdown-menu a { color: #fff; }

.dropdown-menu-item { cursor: pointer; padding: 1em; text-align: center; }

.dropdown-menu-item:hover { background-color: #eb272d; }

@keyframes menu { 0% { opacity: 0; }
  100% { opacity: 1; } }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

:root { font-size: 16px; line-height: 1.4; --font: 'Roboto', sans-serif; --wh: #fff; --bl: #000; }

body { font-family: var(--font); background: var(--wh); }
